.product-list-container {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.product-list-section {
  width: 65%;
  padding-right: 20px;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.product-card {
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.product-card:hover {
  transform: scale(1.05);
}

.product-card h2 {
  font-size: 18px;
  margin-bottom: 8px;
}

.product-card p {
  margin: 5px 0;
}

.product-card button {
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.cart-section {
  width: 30%;
  border-left: 2px solid #ddd;
  padding-left: 20px;
}

.cart-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 15px;
}

.cart-table th,
.cart-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.cart-table th {
  background-color: #f8f8f8;
}

.cart-section h3 {
  margin-top: 15px;
}

.cart-section button {
  padding: 8px 12px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
