/* Auth.css */
.auth-container {
    max-width: 400px;
    margin: 2rem auto;
    padding: 2rem;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px lightblue;
    text-align: center;
  }
  
  .auth-container h2 {
    margin-bottom: 1rem;
    color: #333;
  }
  
  .auth-container input {
    width: 100%;
    padding: 0.75rem;
    margin: 0.5rem 0;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .auth-container button {
    width: 100%;
    padding: 0.75rem;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .auth-container button:hover {
    background-color: #ffa500;
  }
  
  .auth-container p {
    margin-top: 1rem;
    color: #333;
  }
  
  .auth-container p span {
    color: #007bff;
    cursor: pointer;
    text-decoration: underline;
  }
  