/* Cart.css */
.cart-container {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.cart-item {
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
}

button {
  margin-top: 10px;
  padding: 8px 16px;
  background-color: #0070f3;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #005bb5;
}

/* Checkout.css */
.checkout-container {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.cart-summary {
  margin-bottom: 20px;
}

.cart-table {
  width: 100%;
  border-collapse: collapse;
}

.cart-table th, .cart-table td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

h3 {
  margin-top: 20px;
  font-size: 1.2rem;
}

.notification {
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  margin-top: 20px;
  color: green;
}
